body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #F5FAF8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.no--webp .inicio {
  background-image: url(../img/header2.jpg);
}

.webp .inicio {
  background-image: url(../img/header2.webp);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}

.contenido-header {
  padding: 1rem 0 7.5rem 0;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
}

.header .logo__imagen {
  margin: 0 auto;
  width: 8rem;
  height: 8rem;
}
@media (min-width: 769px) {
  .header .logo__imagen {
    margin: 0;
  }
}
.header .titulo {
  font-weight: 700;
  margin: 0;
  color: #0097A7;
}
.header .subtitulo {
  font-weight: 400;
  color: #FFFFFF;
  font-size: 2.5rem;
  margin: 0 0 1.5rem 0;
}
.header .titulo__boton {
  display: block;
  background-color: #1CC5DC;
  color: #FFFFFF;
  padding: 0.5rem 3rem;
  text-transform: uppercase;
  font-size: 2rem;
  border-radius: 3rem;
  width: 25rem;
  margin: 0 auto;
}

.footer {
  background-color: #1CC5DC;
  padding-bottom: 2.5rem;
}
.footer .contenedor--footer {
  padding-top: 1rem;
}
.footer .copyright {
  margin: 1rem 0;
  text-align: center;
  color: #E0E0E0;
}

.footer__contenedor {
  width: min(90%, 30rem);
  margin: 0.5rem auto;
  padding-top: 2rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  /* gap: 2rem; */
}

.footer__iconos {
  height: 4rem;
  width: 4rem;
  font-weight: 400;
  border: 1px solid #E0E0E0;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer--icono {
  padding: 1rem;
  color: #E0E0E0;
}
.footer--icono:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  padding-top: 2rem;
}
@media (min-width: 769px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 769px) {
  .navegacion {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.navegacion a {
  color: #E0E0E0;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
}
@media (min-width: 769px) {
  .navegacion a {
    font-size: 1.8rem;
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.main--titulo {
  font-weight: 700;
  color: #1CC5DC;
  text-align: center;
}

.main--info {
  text-align: center;
  font-weight: 400;
}

@media (min-width: 480px) {
  .iconos--index {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 769px) {
  .iconos--index {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.servicios__info {
  text-align: center;
}
.servicios__info .icon {
  height: 10rem;
}
.servicios__info h3 {
  color: #0097A7;
  font-weight: 700;
  font-size: 2rem;
}
.servicios__info p {
  font-weight: 400;
}

.beneficios {
  justify-content: center;
  align-items: center;
  margin-bottom: 5rem;
}
@media (min-width: 769px) {
  .beneficios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    grid-auto-flow: column;
  }
  .beneficios img {
    grid-column: 1/2;
  }
}
.beneficios img {
  margin-top: 2rem;
}
.beneficios h3 {
  color: #1CC5DC;
  font-weight: 400;
  margin: 0;
}
.beneficios p {
  margin: 0;
  text-align: justify;
}

@media (min-width: 769px) {
  .nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.nosotros h3 {
  color: #0097A7;
  font-weight: 400;
  font-size: 2.5rem;
}
.nosotros p {
  text-align: justify;
}

.sugerencias {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 2rem 0;
}
.sugerencias h1 {
  margin: 0;
  font-weight: 700;
  color: #1CC5DC;
}

@media (min-width: 769px) {
  .alinear {
    display: flex;
    height: 40rem;
  }
}

.preguntas {
  align-items: center;
}
@media (min-width: 769px) {
  .preguntas {
    position: absolute;
    width: 50rem;
    margin-right: 15rem;
    margin-bottom: 5rem;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, 1fr);
  }
  .preguntas:nth-child(even) {
    left: 70rem;
  }
  .preguntas:last-child {
    margin-right: 0;
  }
}
.preguntas .pregunta {
  margin: 2rem 0;
  background-color: #FFFFFF;
  border: 1px solid #E0E0E0;
  padding: 2rem;
  text-align: center;
}
.preguntas .pregunta:hover {
  cursor: pointer;
}
.preguntas .pregunta__informacion {
  border-top: 1px solid #E0E0E0;
  display: none;
}
.preguntas .mostrar {
  display: block;
}
.preguntas .pregunta__titulo {
  color: #1CC5DC;
}
.preguntas .pregunta__titulo:hover {
  cursor: pointer;
}

.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea {
  padding: 0.5rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #E0E0E0;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .boton-contacto {
  background-color: #1CC5DC;
  color: #FFFFFF;
  text-align: center;
  padding: 1rem 3rem;
  margin-top: 2rem;
  border: none;
}
.formulario .boton-contacto:hover {
  background-color: #0097A7;
  cursor: pointer;
}
.formulario .error {
  margin: 0 auto;
  background-color: #b90000;
  text-align: center;
  padding: 1rem;
  color: #FFFFFF;
  text-transform: uppercase;
  max-width: 50rem;
}
.formulario .correcto {
  margin: 0 auto;
  background-color: #59faa9;
  text-align: center;
  padding: 1rem;
  color: #FFFFFF;
  text-transform: uppercase;
  max-width: 50rem;
}

.fondo {
  background-image: url(../img/login.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
  background-position: center center;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.login {
  width: 25rem;
  height: 35rem;
  margin: 15rem 0;
  box-shadow: 0px 0px 28px 9px rgba(102, 102, 102, 0.6);
  -webkit-box-shadow: 0px 0px 28px 9px rgba(102, 102, 102, 0.6);
  -moz-box-shadow: 0px 0px 28px 9px rgba(102, 102, 102, 0.6);
  border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
  background: rgba(155, 154, 154, 0.418);
}
@media (min-width: 480px) {
  .login {
    width: 30rem;
    height: 30rem;
  }
}
@media (min-width: 769px) {
  .login {
    width: 35rem;
    height: 35rem;
    left: 50rem;
    position: absolute;
  }
}
.login h1 {
  color: #E0E0E0;
  font-weight: 700;
  margin-bottom: 0;
}

.form--login, .form--login2, .form--login3 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.form--login .input, .form--login2 .input, .form--login3 .input {
  background-color: rgba(155, 154, 154, 0.418);
  border: 1px solid #E0E0E0;
  border-radius: 1rem;
  padding: 1rem;
  margin: 1rem 0;
}
.form--login .input::placeholder, .form--login2 .input::placeholder, .form--login3 .input::placeholder {
  color: #FFFFFF;
}
.form--login button, .form--login2 button, .form--login3 button {
  display: block;
  color: #FFFFFF;
  font-weight: 400;
  margin-top: 1rem;
  background-color: rgba(155, 154, 154, 0.521);
  width: 19rem;
  padding: 1rem;
  border: 1px solid #E0E0E0;
  border-radius: 1rem;
  box-shadow: 0px 0px 28px 2px rgba(102, 102, 102, 0.6);
}
.form--login button:hover, .form--login2 button:hover, .form--login3 button:hover {
  background-color: rgba(155, 154, 154, 0.911);
}

.form--login2 {
  margin: 3rem 0;
}
.form--login2 .cancelar {
  margin-top: 1rem;
}

.form--login3 {
  margin: 0 0 3rem 0;
  justify-content: flex-start;
}
.form--login3 .cancelar2 {
  margin-top: 1rem;
}
@media (min-width: 769px) {
  .form--login3 .input {
    width: 15rem;
    margin-left: 1rem;
  }
  .form--login3 .input:last-child {
    margin-left: 0;
  }
}

.enlace--form {
  color: #FFFFFF;
  text-decoration: none;
  margin-top: 0.5rem;
  margin-bottom: -0.5rem;
}
.enlace--form:hover {
  color: #1CC5DC;
  cursor: pointer;
}

.ocultar {
  display: none;
}

.mostrar {
  display: flex;
}

.aumentar {
  height: 60rem !important;
}
@media (min-width: 480px) {
  .aumentar {
    height: 50rem !important;
  }
}
@media (min-width: 769px) {
  .aumentar {
    height: 40rem !important;
  }
}
.aumentar .ordenar {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@media (min-width: 769px) {
  .aumentar .ordenar {
    display: inline-block;
  }
}

#div_file {
  position: relative;
  padding: 0.5rem;
  width: 12rem;
  background-color: rgba(155, 154, 154, 0.521);
  border: 1px solid #E0E0E0;
  border-radius: 1rem;
}
#div_file:hover {
  cursor: pointer;
  background-color: rgba(155, 154, 154, 0.911);
}
#div_file #imagen {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
}
#div_file #texto {
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  font-weight: 400;
  font-size: 1.5rem !important;
}

.correoM {
  border: 3px solid #bb2929 !important;
}

.fondo--calendario {
  background-image: url(../img/fondoCalendario.jpg);
  padding: 0;
  margin: 0;
}

.fc-unthemed td.fc-today {
  background-color: rgba(155, 154, 154, 0.521);
}

.fc-center {
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700 !important;
}

.fc-day-header {
  color: #FFFFFF;
}

.fc-day-top {
  color: #FFFFFF;
}

.fc-axis {
  color: #FFFFFF;
}

.menu-collapsed {
  background-color: #343434;
  width: 5rem;
  height: 100%;
  position: fixed;
}
.menu-collapsed .img-P {
  width: 5rem;
}
.menu-collapsed .pr {
  border-radius: 40%;
}
.menu-collapsed .header-m {
  padding: 1rem;
  border-bottom: solid 1px #171717;
}
.menu-collapsed .header-m span {
  color: #FFFFFF;
  display: none;
}
.menu-collapsed .header-m .btn-hamburger {
  background-color: #fff;
  width: 80%;
  height: 2px;
  margin: 0.5rem 0;
}
.menu-collapsed .header-m:hover {
  background-color: #171717;
  cursor: pointer;
}
.menu-collapsed .profile-m {
  padding-top: 0.8rem;
  padding-bottom: 1rem;
  margin: 1rem 0;
  border-bottom: solid 1px #171717;
}
.menu-collapsed .profile-m span {
  color: #FFFFFF;
  text-transform: capitalize;
  display: none;
}
.menu-collapsed .bt {
  margin-left: -1.5rem;
}

.menu-extends {
  background-color: #343434;
  width: 15rem;
  height: 100%;
  position: fixed;
}
.menu-extends .img-P {
  width: 10rem;
}
.menu-extends .pr {
  border-radius: 20%;
}
.menu-extends .header-m {
  padding: 1rem;
  border-bottom: solid 1px #171717;
}
.menu-extends .header-m span {
  text-align: center;
  color: #FFFFFF;
  display: block;
}
.menu-extends .header-m .btn-hamburger {
  background-color: #fff;
  width: 80%;
  height: 2px;
  margin: 0.5rem auto;
}
.menu-extends .header-m:hover {
  background-color: #171717;
  cursor: pointer;
}
.menu-extends .profile-m {
  padding-top: 0.8rem;
  padding-bottom: 1rem;
  margin: 1rem 0;
  border-bottom: solid 1px #171717;
}
.menu-extends .profile-m span {
  text-align: center;
  color: #FFFFFF;
  text-transform: capitalize;
  display: block;
}
.menu-extends .profile-m img {
  margin: 0 auto;
}
.menu-extends .bt {
  width: 10rem;
}