.barra {
    display: flex;
    flex-direction: column;
    padding-top: 2rem;
    @include tablet{
        flex-direction: row;
        justify-content: space-between;
    }
}
.navegacion {
    @include tablet {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    a {
        color: $gris;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        @include tablet {
            font-size: 1.8rem;
            margin-right: 2rem;
            &:last-of-type {
                margin-right: 0;
            }
        }
    }
    
}